<template>
	<view class="scan-page">
		<view class="header">
			<view class="scan-btn" @click="onScan">
				<text class="cuIcon-scan scan-icon"></text>
			</view>
		</view>
		
		<view class="body">
			<view class="top">
				<text class="label">扫描记录</text>
				<text class="delbtn" @click="onClear">清空记录</text>
			</view>
			<scroll-view scroll-y="true" class="scroll-view">
				<block v-for="(item, index) in dataSource" :key="index">
					<view class="cell">
						<view>
							<text class="cuIcon-qrcode icon"></text>
							<text class="label">{{ item.text }}</text>
						</view>
						<text class="cuIcon-delete del-icon" @click="onDel(index)"></text>
					</view>
				</block>
			</scroll-view>
		</view>
	</view>
</template>

<script>
const historyKey = '_history';
export default {
	data() {
		return {
			dataSource: []
		};
	},
	onLoad() {
		const data = uni.getStorageSync(historyKey) || []
		this.dataSource = data
	},
	methods: {
		// 清空所有记录
		onClear() {
			uni.showModal({
				title: '',
				content: '确定清空所有记录吗？',
				cancelText: '手滑了',
				confirmText: '必须滴',
				success: (e) => {
					if(e.confirm) {
						uni.removeStorageSync(historyKey)
						this.dataSource = []
					}
				}
			})
		},
		
		// 删除某一项
		onDel(index) {
			const { dataSource } = this
			uni.showModal({
				title: '',
				content: '确定删除这条数据吗？',
				cancelText: '取消',
				confirmText: '删除',
				success: (e) => {
					if (e.confirm) {
						const arrs = JSON.parse(JSON.stringify(dataSource))
						arrs.splice(index, 1)
						uni.setStorageSync(historyKey, arrs)
						this.dataSource = arrs
					}
				}
			})
		},

		// 扫码
		onScan() {
			uni.scanCode({
				scanType: ['qrCode'],
				success: (e) => {
					this.saveStorage(e)
					uni.setStorageSync('_code_inner', { text: e.result, scanType: e.scanType });
					/* uni.navigateTo({
						url: `/pages/code/index`,
					}) */
				}
			})
		},

		// 扫码结果缓存到本地
		saveStorage(e) {
			const { result, scanType } = e;
			let data = uni.getStorageSync(historyKey)
			const timestep = Date.now()
			if(data) {
				data.unshift({
					time: timestep,
					text: result,
					scanType: scanType,
				})
			} else {
				data = [{
					time: timestep,
					text: result,
					scanType: scanType,
				}]
			}
			this.dataSource = data
			uni.setStorageSync(historyKey, data)
		}
	}
}
</script>

<style lang="scss">
$theme: #f3f5f8;
.scan-page {
	height: 100vh;
	background-color: $theme;
	.header {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 80rpx 0;
		box-shadow: 16rpx 16rpx 40rpx 0 rgba(55,99,170,.1);
		.scan-btn {
			width: 240rpx;
			height: 240rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			box-shadow: 16rpx 16rpx 40rpx 0 rgba(55,99,170,.1);
			border: 4rpx solid #fff;
			border-radius: 50%;
			.scan-icon {
				font-size: 120rpx;
			}
		}
	}
	.body {
		height: calc(100vh - 400rpx);
		.top {
			// height: 88rpx;
			padding-left: 24rpx;
			display: flex;
			align-items: baseline;
			justify-content: space-between;
			.label {
				font-size: 30rpx;
				color: #4b5b76;
				&::before {
					content: "";
					display: inline-block;
					width: 6rpx;
					height: 30rpx;
					background-color: #4b5b76;
					border-radius: 6rpx;
					margin-right: 10rpx;
					position: relative;
					top: 4rpx;
				}
			}
			.delbtn {
				/* height: 88rpx;
				line-height: 88rpx; */
				padding-right: 24rpx;
				padding-top: 30rpx;
				padding-bottom: 4rpx;
				font-size: 28rpx;
				color: #646563;
			}
		}
		.scroll-view {
			height: calc(100% - 88rpx);
			.cell {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding-left: 24rpx;
				.icon {
					color: #4b5b76;
					font-size: 30rpx;
				}
				.label {
					color: #4b5b76;
					font-size: 30rpx;
					margin-left: 10rpx;
				}
				.del-icon {
					font-size: 32rpx;
					padding: 24rpx;
					color: #636465;
				}
			}
		}
	}
}
</style>
